body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;

}



.App {
  text-align: center;
  margin: auto;
  /*width: clamp(200px, 50%, 600px);*/
  background-color: #282c34;
  height: 100vh;
  aspect-ratio: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-size: calc(0.500em + 2vmin);
  color: white;
}

.title {
  margin: 0em;
}

.App-link {
  color: #61dafb;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 0.2em;
}

button.move-button {
  border: 2px outset #D0D3D4;
  background-color: #F0F3F4;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: x-large;
  width: 100%;
  aspect-ratio: 1;
}

button.move-button:hover {
  border-color: #B3B6B7;
  background-color: #D0D3D4;
}

button.move-button:focus {
  border-color: #979A9A;
  background-color: #B3B6B7;
}

button.move-button:active {
  border-color: #7B7D7D;
  background-color: #979A9A;
}

@media screen and (max-width: 575.98px) {
  .App {
    max-height: 100vh;
    aspect-ratio: 9/16;
  }
}
.cube-face {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 4px;
}

.face-0 {
    grid-area: front;
}

.face-1 {
    grid-area: left;
}

.face-2 {
    grid-area: right;
}

.face-3 {
    grid-area: back;
    aspect-ratio: 3/3;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-auto-flow: dense;
}

.face-4 {
    grid-area: top;
}

.face-5 {
    grid-area: bottom;
}
.single-square {
  border: 4px outset white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0;
  aspect-ratio: 1;

}

.color-0 {
  background-color: #2ECC71;
  border-color: #28B463;
}

.color-1 {
  background-color: #E67E22;
  border-color: #CA6F1E;
}

.color-2 {
  background-color: #C0392B;
  border-color: #A93226;
}

.color-3 {
  background-color: #3498DB;
  border-color: #2E86C1;
}

.color-4 {
  background-color: #ECF0F1;
  border-color: #D0D3D4;
}

.color-5 {
  background-color: #F1C40F;
  border-color: #D4AC0D;
}

/* Renders Back face in opposite way */
.face-3>.single-square:nth-child(3n + 1) {
  grid-column: 3;
}

.face-3>.single-square:nth-child(3n + 2) {
  grid-column: 2;
}

@media screen and (max-width: 575.98px) {

  /* Renders Back face columms in direct way */
  .face-3>.single-square:nth-child(3n + 1) {
    grid-column: 1;
  }

  .face-3>.single-square:nth-child(3n + 2) {
    grid-column: 3;
  }

  .face-3>.single-square:nth-child(-n+3) {
    grid-row: 3;
  }

  .face-3>.single-square:nth-last-child(-n+3) {
    grid-row: 1;
  }
}
.opened-cube {
    aspect-ratio: 4/3;
    width: 90%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-template-areas:
        ". top . ."
        "left front right back"
        ". bottom . ."
    ;
    grid-gap: 3%;
    padding-left: 5%;
    padding-right: 5%;
}

@media screen and (max-width: 575.98px) {
    .opened-cube {
        aspect-ratio: 3/4;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(4, 1fr);
        grid-template-areas:
            ". top ."
            "left front right"
            ". bottom ."
            ". back ."
        ;
    }
}
